<template>

    <div
        v-if="currentOrder"
        class="received-loan-container">

        <div class="go-back-link">
            <router-link to="/orders"><span class="icon-arrow-left"></span>Назад</router-link>
        </div>

        <div class="page-title">
            <h1>Кредит «{{currentOrder.name}}»</h1>
        </div>

        <div class="block">

            <div class="block-title">
                <div class="h2">Информация о кредите</div>
            </div>

            <div class="block-content">

                <div class="received-load-info">
                    <ul>
                        <li>
                            <span>Сумма кредита</span>
                            <span>{{parsePay(currentOrder.sum)}} <span class="icon-ruble"></span></span>
                        </li>
                        <li>
                            <span>Срок кредита</span>
                            <span>{{currentOrder.term}} мес.</span>
                        </li>
                        <li>
                            <span>Процентная ставка</span>
                            <span v-if="currentTariff">{{currentOrder.insurance ? currentTariff.rateWithInsurance : currentTariff.rateWithoutInsurance }} %</span>
                        </li>
                        <li>
                            <span>Дата создания</span>
                            <span>{{getDate()}}</span>
                        </li>
                    </ul>
                </div>

            </div>
        </div>

    </div>

</template>

<script>

    import moment from 'moment'
    import { mapState } from 'vuex'

    export default {
        name: "ReceivedLoad",
        data: () => ({
            receivedLoan: {
                id: 1,
                title: 'Быстрый!',
                startDate: '01.01.2021',
                endDate: '01.01.2024',
                sum: 38000000,
                percent: '6.6%',
                paidOut: 17000000
            }
        }),
        computed: {
            ...mapState({
                tariffs: state => state.tariffs.tariffs,
                currentOrder: state => state.createOrder.orderFullInfo.order
            }),
            currentTariff () {
                return this.tariffs.filter(tariff => tariff.id === this.currentOrder.tariff_id)[0]
            }
        },
        methods: {
            parsePay(number) {
                return number.toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ')
            },
            getDate () {
                const res = moment(this.currentOrder.created_at).format('DD.MM.YYYY')
                return res
            }
        },
        async mounted () {
            const id = parseInt(this.$route.params.id)
            await this.$store.dispatch('getCurrentOrder', id)
            if (this.tariffs.length === 0) {
                await this.$store.dispatch('getTariffs')
            }
        }
    }
</script>
